@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

html, body, #root {
  width: 100%;
  height: 100%;
  font-family: Roboto, Arial, sans-serif; }

.mainDiv {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.weatherMain {
  max-width: 1024px;
  display: flex;
  border: 2px solid #26325B;
  border-radius: 5px;
  flex-direction: column;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px; }

.bottom {
  background-image: url(/rays/wp-content/reactapp/static/media/bottom-bg.809673b6.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%; }

.topTitle {
  background-color: #6EA1B7;
  display: inline-flex;
  align-items: flex-start;
  width: 100%;
  justify-content: space-between; }

.title {
  font-size: 14px;
  padding: 10px; }

.inputText {
  margin: 5px; }

.myButton {
  box-shadow: inset 0px 1px 0px 0px #cf866c;
  background-color: #FF4D00;
  border-radius: 3px;
  border: 1px solid #942911;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 12px;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #854629;
  margin: 5px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px; }

.myButton:hover {
  background-color: #FFA700; }

.myButton:active {
  position: relative;
  top: 1px; }

.forecast {
  float: left;
  height: 50%;
  margin: 10px;
  padding: 10px;
  width: 70%; }

.rayHead {
  font-family: 'Luckiest Guy';
  font-size: 18px; }

.raySummary {
  font-size: 12px; }

.weatherWidget {
  text-align: center;
  width: 20%;
  float: right;
  margin: 10px; }

.condition {
  font-size: 1em;
  font-weight: 600; }

.temp {
  font-size: 1.5em; }

.iconWeather {
  margin-top: 5px; }

.updated {
  font-size: 12px; }

.fiveHead {
  font-size: 1.5em;
  padding: 10px; }

.forecastday-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  border-radius: 5px;
  max-width: 20%;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px; }

.forecastCondition {
  font-size: .8em;
  padding: 5px;
  margin: 0 5px; }

.bottom-container {
  margin: 10px;
  padding: 10px; }

